@import "settings"
@import "_mixins/_mixins"
@import "ui"

/* main page */
.full-banner
  @include adaptive-value('margin-top', 16, 8, 1)
  @include adaptive-value('margin-bottom', 16, 8, 1)
  overflow: hidden
  width: 100%
  max-width: 1440px
  margin-left: auto
  margin-right: auto
  .container
    padding: 0
    @media (min-width: 768px)
      padding: 0 15px
  .banner-slide
    height: 600px
    @include adaptive-value('max-height', 780, 593, 1)
    display: flex
    flex-direction: column
    align-items: center
    justify-content: flex-end
    color: #fff
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    padding: 0 16px
    position: relative
    @media (min-width: 768px)
      align-items: flex-start
      height: 505px
    @media (min-width: 960px)
      height: 705px
    @media (min-width: 1200px)
      height: 780px
    &:after
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2))
    &.white-slide
      color: $dark-gray
      @media (min-width: 768px)
        align-items: center
        justify-content: center
        padding-top: 6.75%
      &:after
        display: none
  .banner-wrapper
    @media (min-width: 768px)
      max-width: 55%
  .banner-title
    color: inherit
    text-align: center
    text-transform: uppercase
    font-weight: 600
    @include adaptive-value('font-size', 68, 39, 1)
    @include adaptive-value('line-height', 68, 39, 1)
    letter-spacing: -.05em
    position: relative
    z-index: 1
    @media (min-width: 768px)
      text-align: left
  .banner-subtitle
    color: inherit
    text-align: center
    text-transform: uppercase
    font-weight: 600
    letter-spacing: -.05em
    @include adaptive-value('font-size', 21, 15.75, 1)
    @include adaptive-value('line-height', 21, 15.75, 1)
    margin-bottom: 15px
    position: relative
    z-index: 1
    @media (min-width: 768px)
      text-align: left
  .banner-btns
    @include adaptive-value('padding-top', 32, 22, 1)
    @include adaptive-value('padding-bottom', 82, 16, 1)
    width: 100%
    position: relative
    z-index: 1
    display: flex
    align-items: center
    justify-content: center
    flex-wrap: wrap
    @media (min-width: 768px)
      max-width: 100%
      justify-content: flex-start
    .banner-link
      display: block
      width: 100%
      margin-bottom: 14px
      flex: 0 0 400px
      max-width: 100%
      @media (min-width: 768px)
        flex: 0 0 auto
        width: auto
        padding: 16px 24px
        display: inline-block
        margin: 0 0 14px
        &:nth-of-type(n + 2)
          margin-left: 16px
        &.btn-outline-style
          padding: 15px 24px
  &.center-style
    @media (min-width: 768px)
      .banner-slide
        justify-content: center
        align-items: center
        padding-top: 96px
        .banner-title
          text-align: center
        .banner-subtitle
          text-align: center
        .banner-btns
          justify-content: center
  &.left-center-style
    @media (min-width: 768px)
      .banner-slide
        padding-top: 96px
        justify-content: center
        align-items: flex-start
      .banner-btns
        justify-content: flex-start

.side-banner
  @include adaptive-value('margin-top', 16, 7, 1)
  @include adaptive-value('margin-bottom', 16, 7, 1)
  overflow: hidden
  .container
    padding: 0
    @media (min-width: 768px)
      display: flex
      flex-wrap: nowrap
      align-items: stretch
      justify-content: flex-start
    @media (min-width: 1200px)
      max-width: 1440px
  .banner-slide
    min-height: 800px
    @media (min-width: 768px)
      min-height: 505px
      background-color: $dark-gray
    @media (min-width: 960px)
      min-height: 605px
    @media (min-width: 1200px)
      min-height: 780px
    &__info
      padding: 32px 16px
      background-color: $dark-gray
      color: #fff
      min-height: 106.67vw
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-end
      @media (min-width: 768px)
        flex: 0 0 50%
        max-width: 50%
        min-height: 505px
        padding-bottom: 8%
      @media (min-width: 960px)
        min-height: 605px
      @media (min-width: 1200px)
        min-height: 780px
      @media (min-width: 1440px)
        padding: 32px 32px 95px 140px
      .banner-subtitle
        text-transform: uppercase
        @include adaptive-value('font-size', 21, 15.75, 1)
        @include adaptive-value('line-height', 21, 15.75, 1)
        letter-spacing: -.05em
        font-weight: 600
        margin-bottom: 15px
      .banner-title
        text-transform: uppercase
        @include adaptive-value('font-size', 68, 39, 1)
        @include adaptive-value('line-height', 68, 39, 1)
        letter-spacing: -.05em
        font-weight: 600
        margin-bottom: 32px
    &__image
      background-repeat: no-repeat
      background-position: center
      background-size: cover
      min-height: 106.67vw
      @media (min-width: 768px)
        flex: 0 0 50vw
        max-width: 50vw
        min-height: 505px
      @media (min-width: 960px)
        min-height: 605px
      @media (min-width: 1200px)
        min-height: 780px
        flex: 0 0 50%
        max-height: 50%
  &.reverse-style
    @media (min-width: 768px)
      .container
        flex-direction: row-reverse
      .banner-slide
        &__info
          padding-left: 5.5%
          padding-right: 30px
  & + .side-banner
    @include adaptive-value('margin-top', -16, -7, 1)
    @include adaptive-value('margin-bottom', 16, 7, 1)

.catalog-section
  @include adaptive-value('padding-top', 81, 40, 1)
  @include adaptive-value('padding-bottom', 32, 40, 1)
  color: $dark-gray
  @media (min-width: 768px)
    overflow-x: hidden
  &__content
    @include adaptive-value('margin-top', 48, 30, 1)
    padding: 0 0 0 16px
    @media (min-width: 1200px)
      padding: 0
      width: calc(100vw + 312px)
      margin-left: calc(-312px / 2)
      margin-right: calc(-312px / 2)
  .catalog-slider
    position: relative
    .slide-item
      width: calc(50vw + 12px)
      max-width: 272px
      .image-block
        position: relative
        @media (min-width: 768px)
          overflow: hidden
        .product-link
          display: block
        .product-tags
          position: absolute
          top: 5px
          left: 0
          z-index: 2
          display: flex
          flex-direction: column
          align-items: flex-start
          justify-content: flex-start
          .product-tags__item
            display: inline-block
            color: #fff
            text-transform: uppercase
            font-weight: 600
            font-size: 10px
            line-height: 18px
            padding: 0 8px
            margin: 0px 5px 5px 0
            &.sale-item
              background-color: $red-color
            &.new-item
              background-color: $txt-color
        .product-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
        .product-btns
          display: flex
          align-items: center
          justify-content: flex-end
          position: absolute
          bottom: -16px
          left: 0
          width: 100%
          height: auto
          z-index: 1
          padding: 0 8px
          @media (min-width: 768px)
            position: initial
            bottom: 0
            padding: 0
            margin-top: -46px
          .product-btn
            display: inline-flex
            align-items: center
            justify-content: center
            flex: 0 0 36px
            width: 36px
            height: 36px
            border-radius: 50%
            margin-left: 8px
            background-color: #fff
            color: $dark-gray
            font-size: 22px
            line-height: 22px
            &.addcart-link
              background-color: $dark-gray
              color: #fff
              font-size: 16px
              line-height: 16px
              z-index: 1
              span
                display: none
              .icon
                position: relative
                top: -2px
                left: 1px
              &:hover, &.active
                .icon
                  &:before
                    content: '\e804'
              @media (min-width: 768px)
                flex: 0 0 100%
                width: 100%
                height: 46px
                border-radius: 0
                color: #fff
                margin: 0
                background: rgba($dark-gray, .8)
                text-transform: uppercase
                font-size: 14px
                line-height: 14px
                transform: translateY(50px)
                opacity: 0
                transition: transform .3s, opacity .3s, background-color .3s
                .icon
                  display: none
                span
                  display: inline-block
                &:hover
                  background-color: $dark-gray
            &.favorite-link
              filter: drop-shadow(0px 100px 80px rgba(0, 0, 0, 0.04)) drop-shadow(0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0287542)) drop-shadow(0px 22.3363px 17.869px rgba(0, 0, 0, 0.0238443)) drop-shadow(0px 12.5216px 10.0172px rgba(0, 0, 0, 0.02)) drop-shadow(0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0161557)) drop-shadow(0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0112458))
              @media (min-width: 768px)
                position: absolute
                top: 0
                right: 0
                &:hover, &.active
                  border: 1px solid $light-gray
                  .icon
                    &:before
                      content: '\e80d'
              @media (min-width: 768px)
                top: 5px
                right: 7px
                background-color: transparent
                color: rgba($dark-gray, .3)
                border: none !important
                transition: color .3s
                .icon
                  &:before
                    content: '\e80d'
                &:hover, &.active
                  color: $dark-gray
      .descr-block
        position: relative
        padding-top: 20px
        text-align: center
        @media (min-width: 768px)
          padding-top: 11px
        .product-name
          display: block
          text-transform: uppercase
          color: $dark-gray
          font-weight: 500
          font-size: 14px
          line-height: 14px
          letter-spacing: .02em
          margin-bottom: 12px
          @media (min-width: 768px)
            margin-bottom: 14px
        .product-price
          display: flex
          align-items: center
          justify-content: center
          flex-direction: row-reverse
          width: 100%
          font-weight: 500
          font-size: 14px
          line-height: 14px
          margin-bottom: 10px
          .price
            color: $txt-color
          .oldprice
            text-decoration: line-through
            color: $medium-gray
            margin-left: 15px
            & + .price
              color: $red-color
          @media (min-width: 768px)
            font-size: 16px
            line-height: 16px
            margin-bottom: 12px
        .product-colors
          text-align: center
          display: flex
          flex-wrap: wrap
          align-items: center
          justify-content: center
          @media (min-width: 768px)
            padding-bottom: 5px
          &__all
            flex: 0 0 100%
            max-width: 100%
            width: 100%
            text-align: center
            font-weight: 500
            font-size: 12px
            line-height: 12px
            color: $medium-gray
            text-transform: uppercase
            margin: 8px 0
            @media (min-width: 768px)
              display: inline-block
              flex: 0 0 auto
              width: auto
              margin: 0 0 -2px 10px
          &__item
            display: inline-block
            flex: 0 0 12px
            max-width: 12px
            height: 12px
            width: 12px
            position: relative
            border-radius: 50%
            margin: 0 4px
            cursor: pointer
            &.swiper-pagination-bullet-active
              &:before
                content: ''
                display: block
                width: 16px
                height: 16px
                position: absolute
                top: -2px
                left: -2px
                background-color: inherit
                border-radius: 50%
              &:after
                content: ''
                display: block
                width: 14px
                height: 14px
                position: absolute
                top: -1px
                left: -1px
                background-color: transparent
                border: 1px solid #fff
                border-radius: 50%
      &:hover
        @media (min-width: 768px)
          .image-block
            .product-btns
              .product-btn
                &.addcart-link
                  transform: none
                  opacity: 1
      &.colors-item
        .image-block
          position: relative
          .colors-navigation
            display: none !important // tmp
            position: absolute
            top: calc(50% - 10px)
            left: 0
            z-index: 1
            display: flex
            align-items: center
            justify-content: space-between
            width: 100%
            padding: 0 8px
            color: #fff
            font-size: 20px
            line-height: 20px
            .colors-btn
              display: inline-flex
              align-items: center
              justify-content: center
              flex: 0 0 20px
              width: 20px
              height: 20px
              cursor: pointer
              &.swiper-button-disabled
                display: none
              &.color-prev
                margin-right: auto
              &.color-next
                transform: scale(-1,1)
                margin-left: auto
    .swiper-navigation
      display: flex
      flex-wrap: nowrap
      align-items: center
      justify-content: space-between
      width: 100%
      position: absolute
      height: 1px
      left: -8px
      top: calc(50% - 24px)
      z-index: 1
      padding: 0 16px
      @media (min-width: 768px)
        left: 0
        right: 0
        margin: 0 auto
        max-width: 1128px
        top: calc(50% - 46px)
        padding: 0 16px 0 8px
      .nav-btn
        display: inline-flex
        align-items: center
        justify-content: center
        flex: 0 0 32px
        width: 32px
        height: 32px
        background-color: $dark-gray
        color: #fff
        border: 1px solid transparent
        border-radius: 50%
        font-size: 16px
        line-height: 32px
        cursor: pointer
        &:hover
          border-color: $light-gray
          background-color: #fff
          color: $dark-gray
        @media (min-width: 768px)
          flex: 0 0 40px
          width: 40px
          height: 40px
          line-height: 40px
          font-size: 20px
        &.btn-prev
          margin-right: auto
          transition: transform .3s, opacity .3s, background-color .3s, color .3s, border-color .3s
          transform: translateX(0)
          &.swiper-button-disabled
            transform: translateX(-100px)
            @media (min-width: 768px)
              transform: none
              opacity: .5
          .icon
            position: relative
            right: 1px
        &.btn-next
          margin-left: auto
          transition: transform .3s, opacity .3s, background-color .3s, color .3s, border-color .3s
          transform: translateX(0)
          &.swiper-button-disabled
            transform: translateX(100px)
            @media (min-width: 768px)
              transform: none
              opacity: .5
          .icon
            transform: scale(-1,1)
            position: relative
            left: 1px
  .swiper-pagination
    display: flex
    align-items: center
    justify-content: center
    overflow-x: hidden
    margin-top: 35px
    padding-right: 16px
    z-index: 1
    @media (min-width: 768px)
      margin-top: 41px
      padding-right: 0
    .swiper-pagination-bullet
      display: block
      width: 24px
      height: 2px
      margin: 0 4px
      background-color: rgba($dark-gray, .2)
      transition: background-color .3s
      cursor: pointer
      &-active
        background-color: $dark-gray


/* breadcrumbs section */
.breadcrumbs
  margin: 24px 0 32px
  @media (min-width: 1200px)
    margin: 48px 0
  &__list
    display: flex
    align-items: center
    justify-content: center
    flex-wrap: wrap
  &__item
    flex: 0 0 auto
    width: auto
    font-weight: 500
    font-size: 12px
    line-height: 12px
    letter-spacing: .05em
    text-transform: uppercase
    color: $medium-gray
    position: relative
    margin-bottom: 5px
    &:after
      content: '/'
      display: inline-block
      margin: 0 8px
      @media (min-width: 1200px)
        margin: 0 16px
    &:last-child
      &:after
        display: none
  &__link
    color: $medium-gray
    transition: color .3s
    &:hover
      color: $dark-gray

/* subscribe section */
.subscribe-section
  @include adaptive-value('padding-top', 96, 48, 1)
  @include adaptive-value('padding-bottom', 96, 48, 1)
  &__content
    max-width: 686px
    margin: 0 auto
    @media (min-width: 768px)
      border: 1px solid $medium-gray
      padding: 48px
    .form-title
      font-weight: 400
      text-align: center
      @include adaptive-value('font-size', 28, 21, 1)
      @include adaptive-value('line-height', 28, 21, 1)
      @media (min-width: 768px)
        letter-spacing: -.05em
    .subscribe-form
      margin-top: 19px
      @media (min-width: 768px)
        margin-top: 40px
      .input-group
        width: 100%
        input
          text-transform: uppercase
          text-align: center
          padding: 13px 10px
          font-weight: 500
          @include adaptive-value('font-size', 14, 12, 1)
          @include adaptive-value('line-height', 22, 19, 1)
          display: block
          width: 100%
          border: none
          border-bottom: 1px solid rgba($dark-gray, .1)
          background-color: transparent
          transition: border-color .3s
          @media (min-width: 768px)
            padding: 7px 10px
          &:focus
            border-color: $dark-gray
        &.check-group
          margin-top: 24px
          label
            display: inline-block
            position: relative
            input
              position: absolute
              top: 0
              left: 0
              width: 0
              height: 0
              visibility: hidden
              opacity: 0
              &:checked + span:after
                opacity: 1
            span
              display: inline-block
              padding-left: 28px
              font-size: 12px
              line-height: 19px
              text-transform: uppercase
              position: relative
              @media (min-width: 768px)
                padding-left: 33px
                letter-spacing: .02em
              &:before
                content: ''
                display: block
                width: 16px
                height: 16px
                border: 1px solid $medium-gray
                position: absolute
                top: 0
                left: 0
                @media (min-width: 768px)
                  width: 18px
                  height: 18px
              &:after
                content: '\e805'
                font-family: Fontello
                font-style: normal
                font-weight: normal
                speak: never
                display: inline-flex
                align-items: center
                justify-content: center
                margin: 0
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                position: absolute
                top: 0
                left: 0
                width: 16px
                height: 16px
                background-color: $dark-gray
                color: #fff
                font-size: 12px
                line-height: 16px
                padding-top: 1px
                opacity: 0
                transition: opacity .3s
                @media (min-width: 768px)
                  width: 18px
                  height: 18px
                  line-height: 18px
              a
                font-weight: 600
        &.btn-group
          margin-top: 32px
          text-align: center
          @media (min-width: 768px)
            margin-top: 47px
  &__social
    .social-list
      @include adaptive-value('margin-top', 96, 48, 1)
      display: flex
      align-items: center
      justify-content: center
      flex-wrap: wrap
      &__item
        flex: 0 0 auto
        width: auto
        margin: 0 8px
        @media (min-width: 768px)
          margin: 0 12px
      &__link
        display: inline-flex
        align-items: center
        justify-content: center
        width: 32px
        height: 32px
        font-size: 32px
        line-height: 32px
        color: $dark-gray
        transition: transform .3s
        @media (min-width: 768px)
          width: 38px
          height: 38px
          font-size: 38px
          line-height: 38px
        &:hover
          transform: translateY(-5px)
  &.mpage-subscribe
    @include adaptive-value('padding-top', 64, 8, 1)
    @include adaptive-value('padding-bottom', 96, 46, 1)

