.modal
  display: none
  &.is-open
    display: block
  &[aria-hidden="false"]
    .modal__overlay
      animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1)
    .modal__container
      animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1)
  &[aria-hidden="true"]
    .modal__overlay
      animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1)
    .modal__container
      animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1)
  .modal__container, .modal__overlay
    will-change: transform

.modal__overlay
  position: fixed
  top: 0
  left: 0
  right: 0
  bottom: 0
  background: rgba(0, 0, 0, 0.6)
  display: flex
  justify-content: center
  align-items: center
  z-index: 100

.modal__container
  width: 100%
  height: 100vh
  background-color: #fff
  overflow-y: scroll
  box-sizing: border-box
  position: relative
  margin: 0 auto
  @include scrollbars(0, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
.modal__header
  padding: 24px 40px
  border-bottom: 1px solid $light-gray
  position: relative
  .modal__title
    text-align: center
    font-weight: 600
    font-size: 14px
    line-height: 14px
    text-transform: uppercase
  .modal__close
    display: inline-flex
    align-items: center
    justify-content: center
    width: auto
    background-color: transparent
    border-radius: 0
    border: none
    position: absolute
    top: calc(50% - 12px)
    left: 10px
    font-size: 24px
    line-height: 24px

.modal__content
  padding: 20px 16px

.modal-sizes-table
  .modal__container
    max-width: 680px
    @media (min-width: 680px)
      height: auto
  .modal__header
    @media (min-width: 680px)
      padding: 32px 16px 16px
      border-bottom: none
    .modal__close
      @media (min-width: 680px)
        left: auto
        right: 7px
        top: 11px
        .icon
          &:before
            content: '\e806'
    .modal__title
      @media (min-width: 680px)
        font-size: 28px
        line-height: 28px
        font-weight: 400
        letter-spacing: -.02em
  .modal__content
    @media (min-width: 680px)
      padding: 8px 24px 32px
    .sizes-table
      width: calc(100% + 16px)
      margin: -5px -8px
      border-collapse: collapse
      @media (min-width: 680px)
        width: 100%
        margin: 0 auto
      th,td
        border: 8px solid #fff
      td
        width: 100%
        max-width: 33.3%
        background-color: #F4F4F4
        text-align: center
        padding: 14px 5px
        font-weight: 600
        font-size: 12px
        line-height: 12px
        text-transform: uppercase
        @media (min-width: 680px)
          font-size: 14px
          line-height: 14px
          padding: 16px 5px
    .sizes-parameters
      width: 100%
      margin-top: 22px
      @media (min-width: 680px)
        padding: 0 8px
        margin-top: 18px
      tr:nth-of-type(2n) td
        width: auto
      tr:nth-of-type(1)
        td:nth-of-type(1)
          @media (min-width: 680px)
            text-transform: uppercase
      td
        text-transform: uppercase
        font-weight: 600
        font-size: 12px
        line-height: 14px
        padding: 20px 0
        text-align: center
        border-bottom: 1px solid $light-gray
        @media (min-width: 680px)
          text-transform: none
          font-size: 14px
          line-height: 14px
      td:nth-of-type(1)
        text-align: left
        width: 100%
        max-width: 46%
        padding: 22px 30px 22px 0
    .info
      display: inline-block
      position: relative
      padding-left: 10px
      font-weight: 500
      font-size: 14px
      line-height: 17px
      margin: 24px 0
      color: $medium-gray
      &:before
        content: ''
        display: block
        width: 3px
        height: 3px
        background-color: $medium-gray
        border-radius: 50%
        position: absolute
        top: 7px
        left: 0
.modal-cities
  .modal__container
    max-width: 525px
    @media (min-width: 576px)
      height: auto
  .modal__header
    .modal__close
      @media (min-width: 576px)
        left: auto
        right: 7px
        top: 11px
        .icon
          &:before
            content: '\e806'
    .modal__title
      font-size: 21px
      line-height: 21px
      letter-spacing: -.01em
      font-weight: 400
  .modal__content
    @media (min-width: 576px)
      padding: 16px 32px 32px
    .search-form
      margin-top: 24px
      @media (min-width: 576px)
        margin-top: 0
      input
        display: block
        width: 100%
        border-radius: 0
        border: none
        border-bottom: 1px solid $medium-gray
        font-weight: 500
        font-size: 14px
        line-height: 14px
        padding: 8px 0
        text-transform: uppercase
        color: #000
        background-color: transparent
        @include input-placeholder
          color: rgba($dark-gray, .5)
        @media (min-width: 576px)
          padding: 5px 0 13px
    .cities-list
      margin-top: 20px
      display: flex
      flex-wrap: wrap
      align-items: flex-start
      justify-content: flex-start
      @media (min-width: 576px)
        margin-top: 40px
      &__item
        flex: 0 0 calc(50% - 5px)
        max-width: calc(50% - 5px)
        width: 100%
        margin: 5px 10px 5px 0
        &:nth-of-type(1),
        &:nth-of-type(2)
          .cities-list__link
            font-weight: 600
        &:nth-of-type(2n)
          margin-right: 0
        @media (min-width: 576px)
          flex: 0 0 calc(50% - 10px)
          max-width: calc(50% - 10px)
          margin: 5px 3px 11px 0
      &__link
        display: inline-block
        font-weight: 500
        font-size: 14px
        line-height: 14px
        color: $dark-gray
        transition: color .3s
        padding: 3px 0
        @media (min-width: 576px)
          text-transform: uppercase
        &:hover
          color: #000
.modal-pass
  .modal__container
    max-width: 476px
    @media (min-width: 476px)
      height: auto
    .modal__header
      @media (min-width: 476px)
        border-bottom: none
      .modal__close
        @media (min-width: 476px)
          left: auto
          right: 7px
          top: 11px
          .icon
            &:before
              content: '\e806'
  .modal__content
    @media (min-width: 476px)
      padding: 0 48px 49px
    .change-pass
      width: 100%
      margin-top: 24px
      @media (min-width: 476px)
        margin-top: -5px
    .input-group
      margin-top: 20px
      &:nth-of-type(1)
        margin-top: 0
      @media (min-width: 476px)
        margin-top: 16px
      &.btn-group
        @media (min-width: 476px)
          margin-top: 24px
        button
          display: block
          width: 100%
    label
      display: block
    input
      display: block
      width: 100%
      margin-top: 5px
      font-size: 16px
      line-height: 20px
      padding: 12px 15px
    .label-name
      font-weight: 500
      font-size: 13px
      line-height: 13px
      color: #828282
.modal-delete-product
  .modal__container
    max-width: 376px
    @media (min-width: 376px)
      height: auto
    .modal__header
      @media (min-width: 376px)
        border-bottom: none
        padding-top: 48px
      .modal__close
        @media (min-width: 376px)
          left: auto
          right: 7px
          top: 11px
          .icon
            &:before
              content: '\e806'
  .modal__content
    @media (min-width: 376px)
      padding: 0 16px 40px
    .favorite-link,
    .delete-link
      display: block
      width: 100%
      margin-top: 16px
      @media (min-width: 376px)
        max-width: 236px
        margin: 8px auto
        font-size: 12px
        line-height: 12px
        font-weight: 500
        letter-spacing: .05em
.modal-remember-product
  .modal__container
    max-width: 576px
    @media (min-width: 576px)
      height: auto
  .modal__header
    @media (min-width: 576px)
      border-bottom: none
    .modal__close
      @media (min-width: 576px)
        left: auto
        right: 7px
        top: 11px
        .icon
          &:before
            content: '\e806'
    .modal__title
      @media (min-width: 680px)
        font-size: 28px
        line-height: 28px
        font-weight: 400
        letter-spacing: -.02em
  .modal__content
    @media (min-width: 576px)
      padding: 0 48px 46px
    .modal__title
      font-weight: 400
      font-size: 21px
      line-height: 25px
      letter-spacing: -.02em
      @media (min-width: 576px)
        font-size: 28px
        line-height: 34px
        letter-spacing: -.05em
    .modal__subtitle
      font-weight: 500
      font-size: 13px
      line-height: 17px
      letter-spacing: -.05em
      margin-top: 16px
      @media (min-width: 576px)
        font-size: 14px
        line-height: 20px
    .product-price
      margin-top: 32px
      display: flex
      flex-direction: row-reverse
      align-items: flex-end
      justify-content: flex-start
      @media (min-width: 576px)
        padding-bottom: 14px
        border-bottom: 1px solid $light-gray
      .price-title
        order: 1
        text-transform: uppercase
        font-weight: 500
        font-size: 12px
        line-height: 14px
        margin-right: auto
      .oldprice
        display: inline-block
        text-decoration: line-through
        font-weight: 500
        font-size: 14px
        line-height: 14px
        letter-spacing: -.05em
        margin-left: 10px
        & + .price
          color: $red-color
      .price
        display: inline-block
        font-weight: 500
        font-size: 21px
        line-height: 21px
        letter-spacing: -.05em
        @media (min-width: 576px)
          font-size: 28px
          line-height: 28px
    .input-group
      margin-top: 20px
      @media (min-width: 576px)
        margin-top: 16px
        &:nth-of-type(1)
          margin-top: 28px
      label
        .label-name
          font-weight: 500
          font-size: 13px
          line-height: 13px
        input
          margin-top: 5px
        .choices
          margin-top: 5px
        select
          margin-top: 5px
          width: 100%
          border-radius: 0
          border: 1px solid #E0E0E0
          font-weight: 600
          font-size: 13px
          line-height: 13px
          padding: 12px 10px
      &.btn-group
        @media (min-width: 576px)
          margin-top: 25px
        button
          display: block
          width: 100%

.modal-product
  .modal__container
    max-width: 887px
    @media (min-width: 768px)
      height: auto
  .modal__close
    background-color: transparent
    border: none
    border-radius: 0
    position: absolute
    top: 10px
    right: 6px
    font-size: 24px
    line-height: 24px
  .modal__content
    margin-top: 24px
    @media (min-width: 768px)
      display: flex
      align-items: flex-start
      justify-content: flex-start
      flex-wrap: wrap
    @media (min-width: 887px)
      padding: 0
      margin-top: 0
    &__left-side
      position: relative
      max-width: 386px
      margin: 0 auto
      @media (min-width: 768px)
        flex: 0 0 45%
        max-width: 45%
      @media (min-width: 887px)
        flex: 0 0 386px
        max-width: 386px
      .product-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
      .favorite-link
        display: inline-flex
        align-items: center
        justify-content: center
        flex: 0 0 30px
        width: 30px
        font-size: 24px
        line-height: 24px
        padding: 5px
        color: rgba(36,36,36,.3)
        transition: color .3s
        position: absolute
        top: 10px
        right: 10px
        &:hover
          color: $dark-gray
    &__right-side
      margin-top: 24px
      @media (min-width: 768px)
        flex: 0 0 55%
        max-width: 55%
        margin-top: 0
        padding-top: 24px
        padding-left: 16px
      @media (min-width: 887px)
        flex: 0 0 calc(100% - 386px)
        max-width: calc(100% - 386px)
        padding: 32px 48px
      .product-title
        display: block
        font-weight: 400
        font-size: 22px
        line-height: 22px
        letter-spacing: -.02em
        @media (min-width: 887px)
          font-size: 38px
          line-height: 38px
          letter-spacing: -.05em
      .article
        display: block
        text-transform: uppercase
        font-weight: 500
        font-size: 12px
        line-height: 12px
        letter-spacing: .05em
        margin-top: 10px
        @media (min-width: 887px)
          margin-top: 16px
      .product-price
        display: flex
        align-items: flex-end
        justify-content: flex-end
        flex-direction: row-reverse
        padding-bottom: 16px
        border-bottom: 1px solid $light-gray
        margin-top: 20px
        @media (min-width: 887px)
          margin-top: 32px
          padding-bottom: 31px
        .price-title
          order: 1
          text-transform: uppercase
          font-weight: 500
          font-size: 12px
          line-height: 14px
          margin-right: 80px
        .oldprice
          display: inline-block
          text-decoration: line-through
          font-weight: 500
          font-size: 14px
          line-height: 14px
          letter-spacing: -.05em
          margin-left: 10px
          color: $medium-gray
          @media (min-width: 887px)
            margin-left: 15px
          & + .price
            color: $red-color
        .price
          display: inline-block
          font-weight: 500
          font-size: 21px
          line-height: 21px
          letter-spacing: -.05em
          @media (min-width: 887px)
            font-size: 28px
            line-height: 28px
      .product-properties
        @media (min-width: 887px)
          padding-top: 14px
        &__item
          display: flex
          align-items: center
          justify-content: flex-start
          margin-top: 16px
          @media (min-width: 887px)
            margin-top: 20px
          .property-name
            display: inline-block
            flex: 0 0 30%
            width: auto
            font-weight: 500
            font-size: 12px
            line-height: 12px
            text-transform: uppercase
            color: #828282
          .property-value
            font-weight: 500
            font-size: 14px
            line-height: 14px
            color: $dark-gray
          &.properties-size
            .input-row
              display: flex
              align-items: center
              justify-content: flex-start
              .input-group
                flex: 0 0 auto
                width: auto
                margin: 6px 12px 6px 0
                label
                  display: block
                  position: relative
                  input
                    position: absolute
                    top: 0
                    left: 0
                    width: 0
                    height: 0
                    opacity: 0
                    visibility: hidden
                    &:checked
                      & + span
                        border-color: #000
                        color: #000
                  span
                    display: flex
                    align-items: center
                    justify-content: center
                    height: 32px
                    width: 32px
                    border-radius: 4px
                    border: 1px solid $medium-gray
                    font-weight: 500
                    font-size: 12px
                    line-height: 12px
                    transition: color .3s, border-color .3s
                  &:hover
                    span
                      color: #000

            .size-table-link
              text-transform: uppercase
              font-weight: 500
              font-size: 12px
              line-height: 14px
              text-decoration: underline
              color: $txt-color
              transition: color .3s
              &:hover
                color: #000
          &.properties-color
            .property-value
              display: inline-flex
              align-items: center
              justify-content: flex-start
              span
                display: inline-block
                border: 3px solid #fff
                width: 16px
                height: 16px
                border-radius: 50%
                margin-right: 10px
                position: relative
                &:before
                  content: ''
                  background-color: inherit
                  opacity: .5
                  display: block
                  width: 18px
                  height: 18px
                  position: absolute
                  top: -4px
                  left: -4px
                  border-radius: 50%
                  z-index: -1
          &.properties-color-list
            .input-row
              display: flex
              align-items: center
              justify-content: flex-start
              flex-wrap: wrap
              .input-group
                flex: 0 0 auto
                width: auto
                margin-right: 8px
                label
                  display: inline-flex
                  align-items: center
                  justify-content: flex-start
                  position: relative
                  input
                    position: absolute
                    top: 0
                    left: 0
                    width: 0
                    height: 0
                    opacity: 0
                    visibility: hidden
                    &:checked
                      & + span
                        border: 3px solid transparent
                  span
                    display: inline-block
                    border: 3px solid #fff
                    width: 16px
                    height: 16px
                    border-radius: 50%
                    margin-right: 10px
                    position: relative
                    transition: all .3s
                    &:before
                      content: ''
                      background-color: inherit
                      opacity: .5
                      display: block
                      width: 18px
                      height: 18px
                      position: absolute
                      top: -4px
                      left: -4px
                      border-radius: 50%
                      z-index: -1
      .addform-group
        margin-top: 24px
        @media (min-width: 887px)
          margin-top: 30px
        button, a
          display: block
          width: 100%
          margin-top: 16px
          @media (min-width: 887px)
            margin-top: 8px
.cookie-modal
  position: fixed
  bottom: 16px
  right: 16px
  z-index: 20
  background-color: #fff
  border: 1px solid $medium-gray
  width: calc(100% - 32px)
  max-width: 420px
  transition: transform .3s
  &.show
    transform: translateX(0) !important
  .modal__content
    padding: 24px
  .modal-title
    font-weight: 600
    font-size: 14px
    line-height: 14px
    letter-spacing: .02em
  .modal-info
    font-size: 13px
    line-height: 20px
    letter-spacing: -.02em
    margin-top: 10px
  .modal-btn
    margin-top: 20px
    .cookie-close-btn
      display: block
      width: 100%


/* fix modal gallery */
.fancybox__thumb
  border-radius: 0 !important
.fancybox__thumbs .carousel__slide .fancybox__thumb::after
  border-width: 2px !important
  border-radius: 0 !important
  border-color: $light-gray !important

@media (max-width: 1200px)
  .fancybox__counter
    display: none
  .fancybox__toolbar
    .fancybox__button--zoom,
    .fancybox__button--thumbs,
    .fancybox__button--fullscreen,
    .fancybox__button--slideshow
      display: none !important
  .fancybox__thumbs
    display: none !important
@keyframes mmfadeIn
  from
    opacity: 0
  to
    opacity: 1
@keyframes mmfadeOut
  from
    opacity: 1
  to
    opacity: 0
@keyframes mmslideIn
  from
    transform: translateY(-15%)
  to
    transform: translateY(0)
@keyframes mmslideOut
  from
    transform: translateY(0)
  to
    transform: translateY(10%)