@import "settings"
/* titles */
.page-title
  text-transform: uppercase
  font-weight: 400
  font-size: 28px
  line-height: 28px
  letter-spacing: -.02em
  @media (min-width: 1200px)
    font-size: 38px
    line-height: 38px
.section-title
  text-align: center
  text-transform: uppercase
  color: inherit
  font-weight: 600
  @include adaptive-value('font-size', 38, 27.5, 1)
  @include adaptive-value('line-height', 38, 27.5, 1)
  letter-spacing: -.02em
.swiper-navigation
  height: 0
.nav-btn
  cursor: pointer
select
  cursor: pointer
.input-style
  color: $input-color
  padding: $input-padding
  border: $input-border
  border-radius: $input-border-radius
  background-color: transparent
  transition: border-color .3s, color .3s
  display: block
  width: 100%
  @include input-placeholder
    color: $input-placeholder-color
    transition: color .3s
  &:focus
    border-color: $input-focus-border-color
    @include input-placeholder
      color: transparent
  &.error
    border-color: $input-error-border-color
/* buttons */
.btn-outline-style
  display: inline-block
  color: $dark-gray
  text-transform: uppercase
  text-align: center
  font-weight: 600
  font-size: 14px
  line-height: 14px
  border: 1px solid $dark-gray
  padding: 15px 24px
  transition: background-color .3s, color .3s, border-color .3s
  &:hover
    background-color: $dark-gray
    color: #fff
  &:focus
    color: $dark-gray
    background-color: $light-gray
  &:disabled, &.disabled
    border-color: rgba($dark-gray, .5)
    color: rgba($dark-gray, .5)
  &.white-style
    color: #fff
    border-color: #fff
    &:hover
      color: $dark-gray
      background-color: #fff
    &:focus
      background-color: $light-gray
      color: $dark-gray
    &:disabled, &.disabled
      border-color: rgba(#fff, .5)
      color: rgba(#fff, .5)
.btn-fill-style
  display: inline-block
  color: #fff
  background-color: $dark-gray
  text-transform: uppercase
  text-align: center
  font-weight: 600
  font-size: 14px
  line-height: 14px
  padding: 16px 24px
  transition: background-color .3s, color .3s
  border: none
  &:hover
    background-color: #fff
    color: $dark-gray
  &:focus
    color: $dark-gray
    background-color: $light-gray
  &:disabled, &.disabled
    background-color: rgba($dark-gray, .5)
    color: rgba(#fff, .5)
  &.white-style
    background-color: #fff
    color: $dark-gray
    &:hover
      background-color: $dark-gray
      color: #fff
    &:focus
      color: $dark-gray
      background-color: $light-gray
    &:disabled, &.disabled
      background-color: rgba(#fff, .5)
      color: rgba($dark-gray, .5)

.input-group
  select
    background-color: transparent
  &.info-group
    background-color: rgba($blue-color, .05)
    padding: 16px 16px 16px 52px
    color: $blue-color
    font-weight: 500
    font-size: 14px
    line-height: 14px
    position: relative
    @media (min-width: 1200px)
      padding: 20px 16px 20px 52px
    p
      margin-bottom: 0 !important
    &:before
      content: '\e80e'
      font-family: Fontello
      font-style: normal
      font-weight: normal
      speak: never
      display: block
      margin: 0
      text-decoration: inherit
      text-align: center
      font-variant: normal
      text-transform: none
      position: absolute
      top: 16px
      left: 16px
      font-size: 20px
      line-height: 20px
    &.gray-style
      background-color: rgba($light-gray, .4)
      color: $txt-color

  &.count-group
    display: flex
    align-items: stretch
    justify-content: flex-start
    border: 1px solid rgba(#000, .2)
    border-radius: 2px
    .count-input
      border: none
      flex: 0 0 24px
      width: 24px
      color: $dark-gray
      font-weight: 500
      font-size: 12px
      line-height: 12px
      padding: 10px 0 11px
      text-align: center
      background-color: transparent
    .count-btn
      flex: 0 0 24px
      width: 24px
      display: flex
      align-items: center
      justify-content: center
      color: $medium-gray
      transition: color .3s
      font-size: 8px
      line-height: 8px
      &:hover
        color: $dark-gray
      &.plus-btn
        transform: scale(-1,1)




