.footer
  padding-top: 28px
  background-color: rgba(#F6F6F6, .8)
  text-align: center
  .container
    @media (min-width: 1200px)
      padding: 0
  &__menu
    @media (min-width: 768px)
      display: flex
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
    &__column
      margin-top: 25px
      &:nth-of-type(1)
        margin-top: 0
      @media (min-width: 768px)
        flex: 0 0 33.3%
        max-width: 33.3%
        margin: 63px 0 0 !important
      .column-title
        display: inline-block
        font-weight: 500
        font-size: 14px
        line-height: 14px
        padding-right: 14px
        position: relative
        &:after
          content: '\e812'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: inline-flex
          align-items: center
          justify-content: center
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          transform: scale(1,1)
          transform-origin: center
          position: absolute
          top: 0
          right: 0
          width: 6px
          height: 100%
          font-size: 6px
          line-height: 14px
        @media (min-width: 768px)
          padding-right: 0
          &:after
            display: none
        &.active
          &:after
            transform: scale(1,-1)
          & + .fmenu-list
            transition: height .3s
      .fmenu-wrapper
        max-height: 0
        overflow: hidden
        transition: max-height .3s
        &.show
          max-height: 400px
          transition: max-height .5s
        @media (min-width: 768px)
          max-height: 100% !important
          overflow: visible
      .fmenu-list
        margin: 0
        &__item
          display: block
          width: 100%
          margin-top: 16px
          @media (min-width: 768px)
            &:nth-of-type(1)
              margin-top: 24px
        &__link
          display: inline-block
          text-transform: uppercase
          font-weight: 500
          color: $dark-gray
          font-size: 14px
          line-height: 14px
          @media (min-width: 768px)
            letter-spacing: .02em
          &.disabled
            color: rgba($dark-gray, .5)

  .lang-link
    display: inline-block
    color: $dark-gray
    text-transform: uppercase
    font-weight: 500
    font-size: 12px
    line-height: 12px
    @include adaptive-value('margin-top', 92, 41, 1)
    @media (min-width: 768px)
      font-size: 13px
      line-height: 13px
      letter-spacing: .02em
    span
      font-weight: 600
  .copyright
    text-align: center
    text-transform: uppercase
    @include adaptive-value('font-size', 13, 12, 1)
    @include adaptive-value('line-height', 13, 17, 1)
    font-weight: 500
    border-top: 1px solid rgba($dark-gray, .1)
    padding: 33px 0
    margin-top: 27px
    @media (min-width: 768px)
      padding: 24px 0
      letter-spacing: .02em
    a
      display: inline-block
      text-decoration: none
  .scroll-btn
    position: fixed
    bottom: 16px
    right: 16px
    width: 40px
    height: 40px
    display: flex
    align-items: center
    justify-content: center
    background-color: rgba($dark-gray, .9)
    border: 1px solid $light-gray
    color: #fff
    transition: color .3s, background-color .3s, border-color .3s, transform .3s
    z-index: 10
    &.show
      transform: translateY(0) !important
    .icon
      display: inline-block
      font-size: 24px
      line-height: 24px
      transform: scale(1,-1)
      position: relative
      top: -2px
    &:hover
      background-color: #fff
      border-color: rgba($medium-gray, .5)
      color: $dark-gray