/* titles */
.page-title {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 28px;
  line-height: 28px;
  letter-spacing: -0.02em;
}
@media (min-width: 1200px) {
  .page-title {
    font-size: 38px;
    line-height: 38px;
  }
}

.section-title {
  text-align: center;
  text-transform: uppercase;
  color: inherit;
  font-weight: 600;
  font-size: 38px;
  line-height: 38px;
  letter-spacing: -0.02em;
}
@media (max-width: 1190px) {
  .section-title {
    font-size: calc(27.5px + 10.5 *((100vw - 340px) / 850));
  }
}
@media (max-width: 1190px) {
  .section-title {
    line-height: calc(27.5px + 10.5 *((100vw - 340px) / 850));
  }
}

.swiper-navigation {
  height: 0;
}

.nav-btn {
  cursor: pointer;
}

select {
  cursor: pointer;
}

.input-style {
  color: #565656;
  padding: 10px 10px;
  border: 1px solid #E0E0E0;
  border-radius: 0;
  background-color: transparent;
  transition: border-color 0.3s, color 0.3s;
  display: block;
  width: 100%;
}
.input-style.placeholder {
  color: rgba(86, 86, 86, 0.5);
  transition: color 0.3s;
}
.input-style::-webkit-input-placeholder {
  color: rgba(86, 86, 86, 0.5);
  transition: color 0.3s;
}
.input-style::-moz-placeholder {
  color: rgba(86, 86, 86, 0.5);
  transition: color 0.3s;
}
.input-style:-moz-placeholder {
  color: rgba(86, 86, 86, 0.5);
  transition: color 0.3s;
}
.input-style:-ms-input-placeholder {
  color: rgba(86, 86, 86, 0.5);
  transition: color 0.3s;
}
.input-style:focus {
  border-color: #A8A8A8;
}
.input-style:focus.placeholder {
  color: transparent;
}
.input-style:focus::-webkit-input-placeholder {
  color: transparent;
}
.input-style:focus::-moz-placeholder {
  color: transparent;
}
.input-style:focus:-moz-placeholder {
  color: transparent;
}
.input-style:focus:-ms-input-placeholder {
  color: transparent;
}
.input-style.error {
  border-color: red;
}

/* buttons */
.btn-outline-style {
  display: inline-block;
  color: #565656;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  border: 1px solid #565656;
  padding: 15px 24px;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}
.btn-outline-style:hover {
  background-color: #565656;
  color: #fff;
}
.btn-outline-style:focus {
  color: #565656;
  background-color: #E9E9E9;
}
.btn-outline-style:disabled, .btn-outline-style.disabled {
  border-color: rgba(86, 86, 86, 0.5);
  color: rgba(86, 86, 86, 0.5);
}
.btn-outline-style.white-style {
  color: #fff;
  border-color: #fff;
}
.btn-outline-style.white-style:hover {
  color: #565656;
  background-color: #fff;
}
.btn-outline-style.white-style:focus {
  background-color: #E9E9E9;
  color: #565656;
}
.btn-outline-style.white-style:disabled, .btn-outline-style.white-style.disabled {
  border-color: rgba(255, 255, 255, 0.5);
  color: rgba(255, 255, 255, 0.5);
}

.btn-fill-style {
  display: inline-block;
  color: #fff;
  background-color: #565656;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  padding: 16px 24px;
  transition: background-color 0.3s, color 0.3s;
  border: none;
}
.btn-fill-style:hover {
  background-color: #fff;
  color: #565656;
}
.btn-fill-style:focus {
  color: #565656;
  background-color: #E9E9E9;
}
.btn-fill-style:disabled, .btn-fill-style.disabled {
  background-color: rgba(86, 86, 86, 0.5);
  color: rgba(255, 255, 255, 0.5);
}
.btn-fill-style.white-style {
  background-color: #fff;
  color: #565656;
}
.btn-fill-style.white-style:hover {
  background-color: #565656;
  color: #fff;
}
.btn-fill-style.white-style:focus {
  color: #565656;
  background-color: #E9E9E9;
}
.btn-fill-style.white-style:disabled, .btn-fill-style.white-style.disabled {
  background-color: rgba(255, 255, 255, 0.5);
  color: rgba(86, 86, 86, 0.5);
}

.input-group select {
  background-color: transparent;
}
.input-group.info-group {
  background-color: rgba(47, 128, 237, 0.05);
  padding: 16px 16px 16px 52px;
  color: #2F80ED;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  position: relative;
}
@media (min-width: 1200px) {
  .input-group.info-group {
    padding: 20px 16px 20px 52px;
  }
}
.input-group.info-group p {
  margin-bottom: 0 !important;
}
.input-group.info-group:before {
  content: "\e80e";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: block;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  position: absolute;
  top: 16px;
  left: 16px;
  font-size: 20px;
  line-height: 20px;
}
.input-group.info-group.gray-style {
  background-color: rgba(233, 233, 233, 0.4);
  color: #565656;
}
.input-group.count-group {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}
.input-group.count-group .count-input {
  border: none;
  flex: 0 0 24px;
  width: 24px;
  color: #565656;
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;
  padding: 10px 0 11px;
  text-align: center;
  background-color: transparent;
}
.input-group.count-group .count-btn {
  flex: 0 0 24px;
  width: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #A8A8A8;
  transition: color 0.3s;
  font-size: 8px;
  line-height: 8px;
}
.input-group.count-group .count-btn:hover {
  color: #565656;
}
.input-group.count-group .count-btn.plus-btn {
  transform: scale(-1, 1);
}

